.DinamicSingleMovie {
  display: flex;
  align-items: flex-start;
  height: auto;
  width: 90%; 
  flex-direction: row;
  margin-top: 20px;
  margin-right: 1px;
  flex: 1 1;
}
.DinamicSingleMovie a{
  cursor: pointer;
}

.DinamicSingleMovieImg {
  /* width: 100%; */
  width: 90px;
  height: auto;
  flex: 1 1;
}

.DinamicInfo {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  justify-content: flex-start;
  align-items: flex-start;
}
.DinamicInfo p {
  max-width: 300px;
  margin: unset;
  color: white;
}
.DinamicInfo p:nth-child(1) {
  color: rgb(255, 255, 255);
  text-align: start;
  font-size: 14px;
}
.DinamicInfo p:nth-child(2) {
  font-weight: bolder;
  color: yellow;
  text-align: start;
  font-size: 18px;
  background-image: image();
}
.DinamicInfo p:nth-child(3) {
  margin-bottom: 4px;
}

.DinamicCompMovies {
  background-color: #282E3C;
  border-radius: 22px;
  flex-direction: row-reverse;
  justify-content: center;
  padding: 5px;
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 820px;
  margin-bottom: 25px;
  height: auto;
  flex:1 1;
}

.DinamicCompMoviesPicture {
  width: 100%;
  max-width: 400px;
  height: auto;
  padding: 5px;
  margin-top: 8px;
  flex: 2 1;
}
.DinamicCompMoviesPicture img {
  width: 100%;
  max-width: 400px;
  height: auto;
  padding: 5px;
  margin-bottom: 5px;
}

.DinamicCompMoviesList {
  height: 600px;
  width: 70%;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;  
  margin-left:20px;
  
}
@media (max-width: 960px) {
  .DinamicCompMovies {
    flex-direction: column-reverse;
    width: 420px;
    align-items: center;
    
    height: auto;
  }
  .DinamicSingleMovie {   
    width: 90%;  
  }
  .DinamicCompMoviesList {
    margin-left: 2px;
    width: 70%;
  }

}

@media (max-width: 600px) {
  .DinamicCompMovies {
    flex-direction: column-reverse;
    width: 410px;
    align-items: center;
    
    height: auto;
  }
  .DinamicCompMoviesList {
    margin-left: 30px;
    width: 65%;
  }
}

@media (max-width: 655px) {
  .DinamicInfo p:nth-child(1) {
    color: rgb(255, 255, 255);
    text-align: start;
    font-size: 12px;
  }
  .DinamicInfo p:nth-child(2) {
    font-weight: bolder;
    color: yellow;
    text-align: start;
    font-size: 15px;
  }
  .DinamicCompMoviesList {
    margin-left: 25px;
    width: 90%;
  }
}

@media (max-width: 487px) {
  .DinamicCompMovies {
    flex-direction: column-reverse;
    width: 90%;
  }
  p {
    font-size: 85%;
  }
  .DinamicInfo p:nth-child(1) {
    color: rgb(255, 255, 255);
    text-align: start;
    font-size: 10px;
  }
  .DinamicInfo p:nth-child(2) {
    font-weight: bolder;
    color: yellow;
    text-align: start;
    font-size: 13px;
  }
  .DinamicSingleMovie {   
    width: 100%;  
  }
 
  .DinamicCompMoviesList {
    margin-left: 1px;
    width: 90%;
  }

}




.Modal {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  width: 70%;
  height:70%; 
}

.Overlay {
