.slider {
    max-width: 10rem;
    grid-column-start: 1;
    grid-column-end: 3;
    padding: 0.6rem;
    padding-top: 0.8rem;
    border-radius: 2rem;
}

.slider {
    -webkit-appearance: none;
    height: 15px;
    border-radius: 5px;
    background: #f0efef;
    outline: none;
    opacity: 0.7;
    transition: opacity .2s;
  }
  
  .slider:hover {
    opacity: 1;
  }
  
  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #282E3C;
    cursor: pointer;
  }
  
  .slider::-moz-range-thumb {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #282E3C;
    cursor: pointer;
  }
  
